// import PostImg from "../utils/movieimage";
import StarScore from "./StarScore.vue";
export default {
  name: "MovieCard",
  data: () => ({
    hour: 0,
    minutes: 0,
  }),
  props: {
    index: Number,
    id: String,
    title: String,
    running_time: String,
    score: String,
    description: String,
    director: String,
    producer: String,
    date: String,
    image: String,
    handleText: Function,
    handleLikeMovie: Function,
    handleImgLike: Function,
  },
  components: {
    StarScore,
  },
  methods: {
    likeMovie() {
      this.handleLikeMovie(this.id);
      this.idLike = !this.idLike;
    },
    pad: (number) => {
      if (number < 10) {
        return "0" + number;
      }
      return number;
    },
  },

  computed: {
    backgroundCard: () => {
      return (img) => {
        return {
          "background-image": `url(${img})`,
        };
      };
    },
  },
  created() {
    if (Object.prototype.hasOwnProperty.call(this, "pad")) {
      const min = this.running_time;
      const hour = Math.floor(min / 60);
      const minutes = this.pad(min % 60);
      this.hour = hour;
      this.minutes = minutes;
    }
  },
};
